import React from "react"
import List from "./List"
// React-Component - это основной объект Приложения
class App extends React.Component {


  // Функция, настраивающая React-Component
    constructor(props){
    // Передаём основную настройку в библиотеку React
        super(props)
        // Создаём параметры, которые должны хранится в памяти !ПРИЛОЖЕНИЯ!
        this.state = {
            //Тернарное условия    УСЛОВИЕ ? ПРАВДА : НЕ_ПРАВДА
            items: localStorage.getItem("items") != null ? JSON.parse(localStorage.getItem("items")) : [{"text": "Пора начинать"}]
        };
        this.addTodo = this.addTodo.bind(this);
        this.deleteTodo = this.deleteTodo.bind(this);
    };
  // Event-функция во время событя Submit для добавления нового дела
    addTodo(e){
        e.preventDefault()
        let form = e.target;
        let newTodo = form.todoInput.value;

        this.setState( (state)=>{
            let newState = state

            if (newTodo != ""){
                newState.items.push( {text: newTodo} )
            }

            localStorage.setItem("items", JSON.stringify(newState.items));

            return {newState}
        } );

        form.todoInput.value = ""

    };

    deleteTodo(e){
        let todoIndex = e.target.parentNode.childNodes[0].innerHTML
        // Обновить состояние state, удалить дело по индексуж
        this.setState( (state)=>{
            let newState = state;
            let items = newState.items;

            items.splice((todoIndex - 1),1);
            newState.items = items;

            localStorage.setItem("items", newState.items);

            return {newState}
        } );
    };

  // Рендер нашего компонента
    render(){
        let elem=
            <div id="todo-list" className="container-xl">
                <div className="row justify-content-center">
                    <div className="col-4">
                        <form className="row" onSubmit={this.addTodo}>
                            <input name="todoInput" type="text" className="col form-control" placeholder="Введите дело"/>
                            <button type="submit" className="col-4 btn btn-dark">Добавить</button>
                        </form>
                    </div>
                </div>
                <div className="row justify-content-center">
                    <div className="col-4">
                        <List itemList={this.state.items} deleteTodo={this.deleteTodo}/>
                    </div>
                </div>
            </div>
        return elem
    }
}



export default App;

