function List(props){
    
    let items = props.itemList.map((item, index)=>{
        return <div className="row" style={ {fontSize:"20pt"} }>
        <span className="col-1" style={ {fontSize:"20pt"} }>{index+1}.</span>
        <div className="col-9">{item.text}</div>
        <i className="bi bi-bag-check col-1" onClick={props.deleteTodo}></i>
    </div>
    });

    let elem =
    <div>
        {items}
    </div>

    return elem;
};

export default List;